.rating__stars {
  position: relative;
  display: block;
  font-size: 0;
  width: 68px;
  height: 12px;
}

.rating__stars::before {
  content: "";
  display: inline-block;
  height: 100%;
  background: url("../../img/stars.svg") transparent no-repeat center;
  width: 68px;
  height: 12px;
}

.rating__stars span {
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  height: 100%;
  overflow: hidden;
}

.rating__stars span::before {
  content: "";
  display: inline-block;
  height: 100%;
  background: url("../../img/stars-active.svg") transparent no-repeat center;
  width: 68px;
  height: 12px;
}
